/* breakpoints */
.boxed {
  box-sizing: border-box;
  margin: 20px 0 20px 0px;
}

@media only screen and (min-width: 381px) and (max-width: 1169px) {
  .boxed {
    padding: 0px;
  }
}

@media only screen and (max-width: 380px) {
  .boxed {
    padding: 0px;
  }
}

.boxed > div {
  max-width: 1170px;
  margin: 0 auto !important;
}
